import "../Input/Input.css";
import React from "react";
import logo from '../../images/logo.png';
import { NavLink } from 'react-router-dom'
import Input from "../Input/index";
import CircularProgress from '@material-ui/core/CircularProgress';

function template() {
  const { first_name, last_name, email, password1, password2 } = this.state.dataObj;
  let errorMessage = null;
  let errorMessage1 = null;
  let errorMessage2 = null;
  let errorMessage3 = null;
  let sucess = null;

  if (this.props.email_link) {
    sucess = (
      <span>Click on link to activate Account :{this.props.email_link.detail}</span>
    )
  }

  if (this.props.regis) {
    errorMessage = (
      <span>{this.props.regis.response.data.email}</span>
    );
    errorMessage1 = (
      <span> {this.props.regis.response.data.password1}</span>
    );
    errorMessage2 = (
      <span>{this.props.regis.response.data.password2}</span>
    );
    errorMessage3 = (
      <span>{this.props.regis.response.data.non_field_errors}</span>
    );

  }
  return (
    <div className="body-container"> 
      {this.props.loading ?
        <CircularProgress color="secondary" /> :
        <div className="admin-entry-section">
          <div className="inner-admin">
            <div className="container-fluid">
              <div className="row align-items-center">
                <div className="col-12 col-md-6 p-0 order-md-12 animated slideInLeft">
                  <div className="inner-section register-section">
                    <img src={logo} className="logo" alt="" />
                    <div className="message-conntainer">
                      <h2 className="text-center">Get started</h2>
                      <h4 className="text-center">It's free to signup and only takes a minute.</h4>
                    </div>
                    <p>You have an account?
              <NavLink to="/">Sign In</NavLink></p>
                  </div>
                </div>
                <div className="col-12 col-md-6 p-0 animated slideInRight">
                  <div className="form-section">
                    <h2>Sign up</h2>
                    <div className="alert alert-warning" style={{ padding: 0, margin: 0, border: 0 }}>
                      {errorMessage1}
                      {sucess}
                    </div>
                    <form>
                      <div className="row">
                        <div className="col-12 col-sm-6">
                          <Input label="First name" type='text' className="form-control" id="first_name" val={first_name} placeholder="First name" fnPrepareData={this.fnPrepareData} />
                        
                        </div>
                        <div className="col-12 col-sm-6">
                          <Input label="Last name" type='text' className="form-control" id="last_name" val={last_name} placeholder="Last name" fnPrepareData={this.fnPrepareData} />
                        </div>
                      </div>
                   
                      <Input span={{errorMessage}}  label="Email Address" type='text' className="form-control" id="email" val={email} placeholder="Email Address" fnPrepareData={this.fnPrepareData} />

                      <div className="row">
                        <div className="col-12 col-sm-6">
                         
                          <Input span={{errorMessage2}}label="Password" type='Password' className="form-control" id="password1" val={password1} placeholder="Password" fnPrepareData={this.fnPrepareData} />
                        </div>
                        <div className="col-12 col-sm-6">
                          <Input span={{errorMessage3}} label="Confirm Password" type='Password' className="form-control" id="password2" val={password2} placeholder="Confirm Password" fnPrepareData={this.fnPrepareData} />
                        </div>
                      </div>
                      <div className="form-group required-alert-box">
                        <label>Role</label>
                        <select className="form-control" name="mySelect" onChange={this.handleChange}>
                          <option value="">Select role</option>
                          <option value="is_admin">Admin</option>
                          <option value="is_miner">Miner</option>
                          <option value="read_only">Read Only</option>
                        </select>
                        {/* <span style="display: none">This field is required</span> */}
                      </div>
                      <div className="form-group mb-3">
                        <button type="submit" className="btn btn-green btn-block" onClick={this.submit.bind(this)}>Sign Up</button>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      }

    </div>



  );
};

export default template;
